@font-face {
    font-family: stacker;
    src: url('fonts/stacker-font/Stacker-jE03l.ttf') format('truetype');
    /*By Almarkhatype, License: Freeware, Non-Commercial. */
    /*As found on Fontspace, downloaded 12.12.2022 */
    /*https://www.fontspace.com/stacker-font-f82510 */ }
@font-face {
    font-family: dymaxionScript;
    src: url('fonts/DymaxionScript/DymaxionScript.ttf') format('truetype');
    /*By Almarkhatype, Font License v1.00 */
    /*As found on Fontsquirrel, downloaded 13.12.2022 */
    /*https://www.fontsquirrel.com/fonts/DymaxionScript?q%5Bterm%5D=retro&q%5Bsearch_check%5D=Y */ }
$font-heading: 'stacker';
$font-heading-backup: 'Century Gothic',serif;
$font-heading-accent: dymaxionScript, serif;
$font-heading-span: 'Baguet Script',script;
$font-body: Helvetica,sans-serif;


$font-size-large: 3em;
$font-size-normal: 2em;
$font-size-small: 1em;


$color-theme-Darkgreen: #021F06;
$color-Darkgreen-purpel: #5E116B;
$color-Darkgreen-brown: #6B2411;
$color-Darkgreen-font: #E6E6E6;
$font-accent-color: #FF30F1;


@mixin theme($theme: $color-theme-Darkgreen) {
    $color-primary: $color-theme-Darkgreen;
    $color-accent-One: $color-Darkgreen-purpel;
    $color-accent-Two: $color-Darkgreen-brown;
    $font-color: $color-Darkgreen-font;
    background: $theme;
    color: $font-color;
    .hamburger {
        background-color: $theme; }
    .globalNav {
        background-color: $theme; } }
